html
    font-size: 1rem

body
    /* Common: Removes the default margin from body */
    margin: 0
    /* Common: Sets in common the way of rendering the text */
    text-rendering: optimizeLegibility
    /* Common: Standardizes typography */
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif
    /* Common: Standardizes line height. */
    line-height: 125%

a,
input,
button
    /* Hyperlink: Remove the bottom line. */
    text-decoration: none
    /* Hyperlink: Remove default color */
    color: unset
    -webkit-tap-highlight-color: rgba(0,0,0,0)

/* Pictures: Gives 100% width to fit containers */
img,
picture
    width: 100%

/* Table: Standardize and modernize the appearance */
table
    border-collapse: collapse
    margin-bottom: 10px
    width: 100%

/* Table: Add border */
td,
th
    padding: .4rem
    border: 1px solid black

/* Input: remove border radius */
input
    border-radius: 0

/* Firefox: remove inner focus ring */
button::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="color"]::-moz-focus-inner
    border: 0

a:focus,
textarea:focus,
button:focus,
input:focus
    outline: none

a,
button,
input[type='submit'],
input[type='button'],
input[type='checkbox']
    cursor: pointer

/* iOS: remove zoom double tap */
button
    touch-action: manipulation

/* iOS && Firefox: remove default input appearance */
/* Remove or add any input type */
input[type="button"],
input[type="checkbox"],
input[type="email"],
input[type="file"],
input[type="number"],
input[type="reset"],
input[type="search"],
input[type="submit"],
input[type="tel"],
input[type="text"],
input[type="url"],
textarea,
button
    -moz-appearance: none
    -webkit-appearance: none
    -webkit-tap-highlight-color: transparent

button
    background-color: unset
    border: unset
    color: unset

/* iOS: remove the "cancel button" at the edge of an <input> of type="search" */
input[type="search"]::-webkit-search-cancel-button
    -webkit-appearance: none

input[type="search"],
input[type="date"]
    background-color: unset

/* Remove resize option for textarea */
textarea
    resize: none

/* Firefox: remove default input:invalid appearence */
input:invalid
    box-shadow: none
